'use client';

import { observer } from 'mobx-react-lite';
import { useEffect } from 'react';

import { NotFound } from '@/widgets/not-found/index.js';

import { useStore } from '@/shared/lib/index.js';

export const ArtistNotFoundPage = observer(() => {
    const { artist } = useStore();

    useEffect(() => {
        return () => {
            artist.reset();
        };
    }, [artist]);

    return <NotFound />;
});
