import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement } from 'react';
import { useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';

import { LayoutName, WithClassName, useStore } from '@/shared/lib/index.js';

import { useHistoryBackOrForward } from '../lib/useHistoryBackOrForward.js';

import styles from './NavigationControls.module.scss';

interface Props extends WithClassName {
    withBackwardControl?: boolean;
    withForwardControl?: boolean;
    withBackwardFallback?: string;
}

export const NavigationControls = observer(
    ({
        withBackwardControl = true,
        withForwardControl = true,
        className,
        withBackwardFallback
    }: Props): ReactElement => {
        const { settings } = useStore();

        const isMobile = settings.layout === LayoutName.Mobile;
        const { formatMessage } = useIntl();
        const { canBack, canForward, moveBack, moveForward } = useHistoryBackOrForward(withBackwardFallback);

        return (
            <div className={clsx(styles.root, className)}>
                {withBackwardControl && (
                    <Button
                        aria-label={formatMessage({ id: 'navigation.go-back' })}
                        radius="round"
                        disabled={!canBack}
                        size={isMobile ? 'xxs' : 's'}
                        icon={<Icon size="xxs" variant="arrowLeft" />}
                        onClick={moveBack}
                    />
                )}
                {withForwardControl && (
                    <Button
                        aria-label={formatMessage({ id: 'navigation.go-forward' })}
                        radius="round"
                        disabled={!canForward}
                        size={isMobile ? 'xxs' : 's'}
                        icon={<Icon size="xxs" variant="arrowRight" />}
                        onClick={moveForward}
                    />
                )}
            </div>
        );
    }
);
