'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { notFound } from 'next/navigation.js';
import { ReactElement, use, useEffect, useMemo, CSSProperties } from 'react';
import { useIntl } from 'react-intl';

import { YaMusicContextData, YaMusicContexts } from '@yandex-music-int/player-contexts';
import { MusicEntityData } from '@yandex-music-int/player-entities';
import { useElementOffsetY } from '@yandex-music-int/sol/common/hooks/useElementOffsetY.js';
import { ScrollableContent } from '@yandex-music-int/sol/components/ScrollableContent';
import { PlayContextParams } from '@yandex-music-int/sonata';

import { Header } from '@/widgets/content/index.js';
import { PageHeaderArtist, PageHeaderShimmer } from '@/widgets/page-header/index.js';

import { AlbumsCarousel, AlbumCard } from '@/features/album/index.js';
import { ArtistCard } from '@/features/artist/index.js';
import { PlaylistsCarousel } from '@/features/playlist/index.js';
import { TrackPlaylist, TrackWithArtistsModel } from '@/features/track/index.js';

import { ArtistModel } from '@/entities/artist/index.js';
import { hasDuplicatesBefore } from '@/entities/playlist/index.js';

import {
    LoadingState,
    TrackVariant,
    useStore,
    getAverageBackgroundColor,
    useContainer,
    CONTAINER_COOKIE_STORAGE,
    useRumPage,
    AnalyticsPageProvider,
    AnalyticsBlock,
    AnalyticsPage,
    useCreateFrom,
    Experiments
} from '@/shared/lib/index.js';
import { CarouselBlock, BlockHeader, EntityCardShimmer, TracksList, Footer } from '@/shared/ui/index.js';

import styles from './Artist.module.scss';

interface Props {
    artistId?: string | number;
}

const HEADER_TO_VIEWPORT_OFFSET_NEW_NAVIGATION = 93;
const HEADER_TO_VIEWPORT_OFFSET_OLD_NAVIGATION = 17;

export const ArtistPage = observer(({ artistId }: Props): ReactElement => {
    const from = useCreateFrom({ pageId: AnalyticsPage.ARTIST, blockId: AnalyticsBlock.ARTIST });
    const container = useContainer();
    const cookies = container.get(CONTAINER_COOKIE_STORAGE);
    let isWebNextNavigationOn = false;

    if (process.env.BUILD_TYPE === 'web') {
        isWebNextNavigationOn = cookies.get('WebNextNavigation') === 'on';
    }

    const { artist, slides, experiments } = useStore();
    const { formatMessage } = useIntl();

    const { ref, offsetY } = useElementOffsetY();

    const averageColor = useMemo(() => {
        return getAverageBackgroundColor(artist?.meta?.artist?.averageColor);
    }, [artist?.meta?.artist?.averageColor]);

    const headerStyle = useMemo(() => {
        if (offsetY !== undefined) {
            const initialTranslateHeader = isWebNextNavigationOn
                ? HEADER_TO_VIEWPORT_OFFSET_NEW_NAVIGATION
                : HEADER_TO_VIEWPORT_OFFSET_OLD_NAVIGATION;
            const translate = offsetY - initialTranslateHeader;
            return {
                '--average-color-background': averageColor,
                transform: `translateY(${offsetY >= initialTranslateHeader ? 0 : translate}px)`,
                opacity: 1
            };
        }
        return {};
    }, [offsetY, isWebNextNavigationOn, averageColor]);

    useEffect(() => {
        return () => {
            if (artist.id !== artistId) {
                artist.reset();
            }
        };
    }, [artist, artistId]);

    if (artist.loadingState === LoadingState.IDLE) {
        use(
            artist.getData({
                artistId
            })
        );
    }

    useRumPage(artist.loadingState === LoadingState.RESOLVE);

    if (artist.isNotFound) {
        notFound();
    }

    const coverLink = useMemo(() => {
        if (experiments.checkExperiment(Experiments.WebRewind2023, 'on') && slides.artistItems.length > 0) {
            return `/rewind2023/artist/${artistId}`;
        }
        return;
    }, [artistId, experiments, slides.artistItems.length]);

    const playContextParams = (id: string): PlayContextParams<YaMusicContextData, MusicEntityData> => {
        return {
            contextData: {
                type: YaMusicContexts.Artist,
                meta: {
                    id: Number(artistId)
                },
                from
            },
            queueParams: {
                index: artist.popularTracks.findIndex((item: TrackWithArtistsModel) => item.id === id)
            },
            loadContextMeta: true
        };
    };

    const artistHeader = useMemo(() => {
        if (artist.isLoading) {
            return <PageHeaderShimmer className={styles.header} coverRadius="round" />;
        }
        return <PageHeaderArtist className={styles.header} artistMeta={artist.meta} coverLink={coverLink} ref={ref} />;
    }, [artist.isLoading, artist.meta, coverLink, ref]);

    return (
        <AnalyticsPageProvider pageId={AnalyticsPage.ARTIST}>
            <div className={styles.averageColorBackground} style={headerStyle as CSSProperties} />
            <ScrollableContent className={styles.root} containerClassName={styles.content}>
                <div>
                    <Header />
                    {artistHeader}
                    <div className={styles.recents}>
                        {artist.isPopularTracksExist && (
                            <div
                                className={clsx(styles.popularTracksBlock, {
                                    [styles.popularTracksBlock_withLastRelease]: artist.isLastReleaseExists
                                })}
                            >
                                <BlockHeader
                                    className={clsx(styles.carouselContainer, styles.important)}
                                    title={formatMessage({ id: 'entity-names.popular-tracks' })}
                                />
                                <TracksList
                                    className={clsx(styles.popularTracksContainer, styles.tracksContainer)}
                                    isLoading={artist.isLoading}
                                    variant={TrackVariant.PLAYLIST}
                                >
                                    {artist.popularTracks?.map((track: TrackWithArtistsModel, index: number) => {
                                        return (
                                            <TrackPlaylist
                                                key={track.id}
                                                track={track}
                                                playContextParams={playContextParams(track.id)}
                                                hasDuplicatesBefore={hasDuplicatesBefore(
                                                    artist.popularTracks,
                                                    track,
                                                    index
                                                )}
                                            />
                                        );
                                    })}
                                </TracksList>
                            </div>
                        )}
                        {artist.isLastReleaseExists && (
                            <div className={styles.lastReleaseBlock}>
                                <BlockHeader
                                    className={clsx(styles.lastReleaseHeader, styles.important)}
                                    title={formatMessage({ id: 'entity-names.recently-release' })}
                                />
                                {artist.isLoading ? (
                                    <EntityCardShimmer className={styles.lastReleaseCard} />
                                ) : (
                                    <AlbumCard className={styles.lastReleaseCard} album={artist.lastRelease} />
                                )}
                            </div>
                        )}
                    </div>
                    <div className={styles.carouselBlocks}>
                        {artist.isPopularAlbumsExists && (
                            <AlbumsCarousel
                                headerClassName={styles.carouselHeader}
                                containerClassName={clsx(styles.carouselContainer, styles.important)}
                                albums={artist.popularAlbums}
                                isLoading={artist.isLoading}
                                title={artist.popularAlbumsTitle}
                            />
                        )}
                        {artist.isPlaylistsExist && (
                            <PlaylistsCarousel
                                isLoading={artist.isLoading}
                                title={formatMessage({ id: 'entity-names.artist-playlist' })}
                                playlists={artist.playlists}
                                containerClassName={clsx(styles.carouselContainer, styles.important)}
                                headerClassName={styles.carouselHeader}
                            />
                        )}
                        {artist.isSimilarArtistsExist && (
                            <CarouselBlock
                                title={formatMessage({ id: 'entity-names.similar-artists' })}
                                isLoading={artist.isLoading}
                                headerClassName={styles.carouselHeader}
                                containerClassName={clsx(styles.carouselContainer, styles.important)}
                            >
                                {artist.similarArtists.map((artistItem: ArtistModel) => (
                                    <ArtistCard key={artist.id} artist={artistItem} contentLinesCount={3} />
                                ))}
                            </CarouselBlock>
                        )}
                    </div>
                </div>
                <Footer className={styles.footer} />
            </ScrollableContent>
        </AnalyticsPageProvider>
    );
});
