import { debounce } from 'lodash-es';
import { useMemo } from 'react';

import { useHistory } from '@/shared/lib/index.js';

const DEBOUNCE_DURATION_MS = 200;

export const useHistoryBackOrForward = (
    withBackwardFallback?: string
): {
    canBack: boolean;
    canForward: boolean;
    moveBack: () => void;
    moveForward: () => void;
} => {
    const history = useHistory();
    const moveBack = useMemo(() => {
        return debounce(() => {
            if (withBackwardFallback && !history.canBack) {
                history.replaceState({
                    href: withBackwardFallback,
                    primaryPage: {
                        pathname: '',
                        query: ''
                    }
                });
                return;
            }
            history?.back();
        }, DEBOUNCE_DURATION_MS);
    }, [history, withBackwardFallback]);

    const moveForward = useMemo(() => {
        return debounce(() => {
            history?.forward();
        }, DEBOUNCE_DURATION_MS);
    }, [history]);

    return {
        canBack: Boolean(withBackwardFallback) || history.canBack,
        canForward: history.canForward,
        moveBack,
        moveForward
    };
};
